import React, {useContext, useState, useRef, useEffect} from 'react';
import ProductContext from "../Context/Product_Context";
import {BASE_API, BASE_URL, fetchPost, SHIPPING_COST} from "../Utils/Constants";
import Swal from 'sweetalert';
import { withRouter } from 'react-router-dom';
import DatePicker from "react-datepicker2";
import { registerLocale } from  "react-datepicker";
import es from 'date-fns/locale/es';

registerLocale('es', es)

const Cart = () => {

  const { cart, cartCount, cartTotal } = useContext(ProductContext);
  const [value, setValue] = useState('');
  const comments = useRef(null);
  const [ clicking, setClicking ]= useState("block");
  const [ showp, SetShowp ]= useState("none");

  const dateNow = new Date().setDate(new Date().getDate());
  const [methodSelected,SetMethodSelected] = useState(11);
  const orderNumber = Math.floor(Math.random() * 1000) + 1;

  const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
    minimumFractionDigits: 2
  });

  const paymentSuccess = e => {
    console.log("ORDER COMPLETE");
    sendOrder();
  }

  const validateOrder = () => {
    const user = JSON.parse(localStorage.getItem('user'));
    let isValid = true;
    if(user === null) {
      Swal('', 'Debes de iniciar sesión para poder hacer el pedido', 'error');
      isValid = false;
    }
    else {
      if (value === '' || value === null || value === undefined) {
        Swal('', 'Debes seleccionar la fecha del pedido', 'error');
        isValid = false;
      }

      if (cartTotal < 500) {
        Swal('', 'El pedido debe ser mayor a $500.00 (sin contar el costo de envío)', 'warning');
        isValid = false;
      }

      if (methodSelected === 11) {
        Swal('', 'Debes seleccionar un método de pago.', 'warning');
        isValid = false;
      }
    }
    return isValid;
  }

  const sendOrder = () => {
    const user = JSON.parse(localStorage.getItem('user'));
    const order = {
      num_items:cartCount,
      subtotal: cartTotal,
      shipping_cost:SHIPPING_COST,
      total:Number(cartTotal) + Number(SHIPPING_COST),
      status_payment:methodSelected,
    };

    const extraData = {
      comments: comments.current.value,
      delivery_data: value
    };

    const products = [...cart];
    const orderData = {
      order,
      products,
      extraData
    };

    //console.log("ORDER ", orderData)

    const formData = new FormData();
    formData.append('order',JSON.stringify(orderData));

    SetShowp("block");
    setClicking("none");

    fetchPost('registerOrder',formData, user.token)
      .then(response => {
        if(response.error) {
          Swal('', response.message, 'error');
          SetShowp("none");
          setClicking("block");
        }else{

          Swal('',response.message,'success');
          localStorage.removeItem('cart');
          localStorage.removeItem('count');
          localStorage.removeItem('total');
          setTimeout(()=> {
            window.location.href = '/';
          },1000);

        }
      }).catch(error =>{
      SetShowp("none");
      setClicking("block");
    });
  }

  const submitOrder = () => {
    const flag = validateOrder();
    if (flag) sendOrder();
  };

  const banamexPay = () => {
    const flag = validateOrder();
    if (flag) window.Checkout.showLightbox();
  }

  const decodeFormParams = params => {
    const pairs = params.split('&'),
      result = {};

    for (let i = 0; i < pairs.length; i++) {
      let pair = pairs[i].split('='),
        key = decodeURIComponent(pair[0]),
        value = decodeURIComponent(pair[1]),
        isArray = /\[\]$/.test(key),
        dictMatch = key.match(/^(.+)\[([^\]]+)\]$/);

      if (dictMatch) {
        key = dictMatch[1];
        const subkey = dictMatch[2];

        result[key] = result[key] || {};
        result[key][subkey] = value;
      } else if (isArray) {
        key = key.substring(0, key.length-2);
        result[key] = result[key] || [];
        result[key].push(value);
      } else {
        result[key] = value;
      }
    }

    return result;
  }

  const mapItems = () => cart.map(item => ({name:item.name, quantity: item.count, unitPrice:item.unit_price}));

  /*useEffect(() => {
    fetch(`${BASE_API}get/session/${orderNumber}`)
      .then(response => response.text())
      .then(response => {
        const data = decodeFormParams(response);
      //  console.log('RESPONSE', data);
        window.Checkout.configure({
          merchant: 'TEST1140019',
          session: {
            id: data['session.id']
          },
          order: {
            amount: Number(cartTotal) + Number(SHIPPING_COST),
            currency: 'MXN',
            description: 'Productos ordenados',
            id: orderNumber,
            item: mapItems(),
            itemAmount: Number(cartTotal),
            shippingAndHandlingAmount: Number(SHIPPING_COST)
          },
          interaction: {
            operation: 'PURCHASE', // set this field to 'PURCHASE' for Hosted Checkout to perform a Pay Operation.
            displayControl: {
              billingAddress: 'HIDE',
              orderSummary: 'SHOW',
              shipping: 'READ_ONLY'
            },
            merchant: {
              name: 'HECHOS LA RAYA',
              address: {
                line1: 'Calle San Miguel 464',
                line2: 'Jardines de Los Arcos, 44500 Guadalajara, Jal'
              }
            }
          }
        });
      }).catch(error => {
      console.log("ERRROS", error);
    });
  },[])*/

  const BtnCart = () => {
    return (
      cartCount > 0 ? (
        <div className="cartBtn2">
          <div style={{display:clicking}}>
        {/*    <button type="button" className="btn btnTheme" onClick={() => methodSelected === 1 ? banamexPay() : submitOrder()}>
              { methodSelected === 1 ? 'Pagar pedido' : 'Levantar pedido'}
            </button>*/}
            <button type="button" className="btn btnTheme" onClick={() => submitOrder(cart, cartCount)}> Levantar pedido </button>
          </div>
          <div style={{display:showp}}>
            <p  >Procesando orden...</p>
          </div>
        </div>
      ) : '' )
  };

  const onMethodChanged = (e) => {
    SetMethodSelected(parseInt(e.currentTarget.value));
  };

  return (
    <section className="naturalInner">
      <div className="container">
        <div className="row">
          <div className="headingOther">
            <h2>PRODUCTOS EN EL<span> CARRITO</span></h2>
          </div>
        </div>
        <div className="row">
          <div className="cart-back table-responsive">
            <table className="table table-bordered table-cont2">
              <thead>
              <tr>
                <th className="th">Imagen</th>
                <th className="th">Producto</th>
                <th className="th">Cantidad</th>
                <th className="th">Precio</th>
              </tr>
              </thead>
              <tbody>
              {
                cart.map(product => {
                  return (
                    <tr key={product.id}>
                      <td align="center"><img className="img-responsive" style={{height:40}} src={product.id > 9999 ? product.image :  BASE_URL + product.image} alt=""/></td>
                      <td align="center">{product.name}</td>
                      <td align="center">{product.count + ' ' + product.unit} </td>
                      <td align="center">{formatter.format(product.productSelectedPrice)} </td>
                    </tr>
                  )
                })
              }

              </tbody>
            </table>
            <div className='row'>
              <form className='form-inline'>
                <div className='col-md-3'>
                  <input type="text" ref={comments} className="form-control" placeholder="Comentarios (máximo 150)" style={{ width: '100%' }} maxLength="150"/>
                </div>
                <div className='col-md-4'>
                  <select className="form-control" placeholder="Comentarios" style={{width:'100%'}} value={methodSelected} onChange={onMethodChanged}>
                    <option value={11} >Selecciona un método de pago</option>
                    {/*<option value={1} >Tarjeta de débito/crédito</option>*/}
                    <option value={0} > Efectivo</option>
                  </select>
                </div>
                <div className='col-md-3'>
                  <div className="form-group">
                    <DatePicker
                      min={dateNow}
                      locale="es"
                      className="form-control"
                      inputFormat="D-M-YYYY"
                      input={true}
                      placeholder="Fecha entrega"
                      isGregorian={true}
                      value={value}
                      onChange={value => setValue(value)} />
                  </div>
                </div>
                <div className='col-md-2'>
                  <BtnCart/>
                </div>
              </form>
            </div>
            <div className='row'>
              <p style={{float:'right', paddingTop:10, paddingRight:10}}>Nota: Por favor verifica que tu orden esté completa antes de "levantar pedido".</p>
            </div>
            <div className='row' style={{float:'right', paddingTop:10, paddingRight:10}}>
              <p>Subtotal: {formatter.format(cartTotal)}</p>
              <p>Costo de envío: {formatter.format(SHIPPING_COST)}</p>
              <p>Total: {formatter.format(Number(cartTotal) + Number(SHIPPING_COST))}</p>
            </div>
            <input type='hidden' id='payment' onClick={paymentSuccess}/>
          </div>
        </div>
      </div>
    </section>
  )
};

export default withRouter(Cart);
